$label-color: rgba(28, 1, 255, 1);
@mixin clearfix() {
  &::after {
    display: block;
    content: "";
    clear: both;
  }
}

:global(#masterstudy_ui_root) {
  position: relative;
  * {
    box-sizing: border-box;
  }

  padding-right: 10px;
  max-width: 1200px;

}

.loader {
  position: absolute;
  width: 100%;
  display: flex;
  height: 100%;
  justify-content: center;
  justify-items: center;
  align-items: center;
  background-color: #ffffff5e;
}
